@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC);
.outerModal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.innerModal {
  position: fixed;
  top: 50px;
  right: 50px;
  bottom: 50px;
  left: 50px;
  padding: 50px 50px 50px 50px;
  box-shadow: 5px 5px 10px 0px rgb(0, 0, 0);
  background: rgba(216, 216, 216, 1);
  border-radius: 20px;
  font-size: 1em;
  font-family: "Noto Sans TC", sans-serif;
  color: black;
  text-align: center;
}

.title {
  font-size: 1.5em;
  color: black;
  padding: 10px 10px 10px 10px;
}

.innerContent {
  color: rgb(50, 50, 50);
  /* border: 1px solid; */
  width: 100%;
  height: 90%;
  display: inline-flex;
  flex-wrap: wrap;
  position: relative;
  justify-content: center;
}

/* http://jsfiddle.net/marcus211/enxqkd7w/ */
.scrollContent {
  color: rgb(50, 50, 50);
  /* border: 1px solid; */
  width: 45%;
  height: 100%;
  margin: 10px 10px 10px 10px;
  overflow: hidden;
  text-align: justify;
}

.hiddenScroll {
  overflow-x: hidden;
  overflow-y: scroll;
  height: 100%;
  margin: 0px -300px 0px 0px;
  padding-right: 300px;
}

.mapContent {
  color: rgb(50, 50, 50);
  /* border: 1px solid; */
  width: 45%;
  height: 100%;
  margin: 0px 0px 0px 0px;
}

.crossButton {
  cursor: pointer;
  position: fixed;
  right: 10px;
  top: 10px;
  font-size: 1.5em;
  font-family: "Noto Sans TC", sans-serif;
  color: silver;
  z-index: 700;
}

.crossButton:hover {
  color: rgb(255, 255, 255);
}

@media screen and (max-width: 800px) {
  .innerModal {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 5%;
    box-shadow: 0px 0px 0px 0px rgb(0, 0, 0);
    background: rgb(200, 200, 200, 0.8);
    border-radius: 0px;
  }

  .innerContent {
    width: 100%;
    height: 90%;
  }

  /* http://jsfiddle.net/marcus211/enxqkd7w/ */
  .scrollContent {
    width: 100%;
    height: 55%;
  }

  .mapContent {
    width: 100%;
    height: 40%;
  }

  .crossButton {
    color: rgb(0, 0, 0);
  }

  .crossButton:hover {
    color: rgb(90, 90, 90);
  }
}
/* https://fonts.google.com/specimen/Noto+Sans+TC?selection.family=Noto+Sans+TC */

.mainStyle {
    /*main shape*/
    width: 100%;
    min-height: 100%;

    /*main color*/
    background-color: rgba(0, 0, 0, 0.7);

    /*main content*/
    display: inline-flex;
    flex-wrap: wrap;
    position: relative;
    justify-content: center;
}

.boxStyle {
    /*box shape*/
    width: 300px;
    height: 80px;
    border-style: none;
    border-radius: 10px;
    box-shadow: 1px 1px 2px 0px rgb(0, 0, 0);

    /*box color*/
    background-color: rgb(216, 216, 216, 0.5);

    /*box content*/
    padding: 25px 25px 25px 25px;
    margin: 25px 25px 25px 25px;

    /*text content*/
    font-size: 0.9em;
    color: black;
    text-align: center;

    /*CSS animate*/
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.boxStyle:hover {
    background-color: rgb(216, 216, 216, 0.7);
    box-shadow: 2.5px 2.5px 5px 0px rgb(0, 0, 0);
}

@media screen and (max-width: 600px) {
    .mainStyle {
        /*main shape*/
        width: 100%;
        border-style: none;
        border-radius: 0px;
        border-width: 0px;
    }

    .boxStyle {
        /*box shape*/
        max-width: 100%;
        border-style: none;

        /*box color*/
        /* background-color: rgb(216, 216, 216, 0.7);
        box-shadow: 1px 1px 2px 0px rgb(0, 0, 0); */

        /*box content*/
        padding: 15px 15px 15px 15px;
        margin: 15px 15px 15px 15px;
    }
}
/*uparrow*/
.uparrow {
  color: rgb(255, 255, 255);
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  position: fixed;
  font-size: 3em;
  bottom: 20px;
  right: 20px;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  z-index: 1000;
}

.uparrow.arrowmove {
  right: -200px;
}

.uparrow:hover {
  color: rgb(200, 230, 235);
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.5);
  -webkit-transition: scale(1.5);
  transition: scale(1.5);
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.uparrow:active {
  color: rgb(0, 80, 140);
}

.BoxExtend {
    width: 100%;
    height: 100vh;
}

.searchSetup {
    width: 300px;
    height: 250px;
    padding: 10px 10px 10px 10px;
    border-width: 2px;
    border-style: none;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}

.inputSetup {
    width: 200px;
    border-radius: 10px;
    background-color: rgb(108, 108, 108);
    padding: 10px 10px 10px 10px;
    box-shadow: 1px 1px 2px 0px rgb(0, 0, 0);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.inputSetup:hover {
    background-color: rgb(162, 162, 162);
    box-shadow: 2px 2px 4px 0px rgb(0, 0, 0);
}

.inputSetup input {
    width: 100%;
    height: 30px;
    font-size: 1em;
    border-radius: 5px;
}
